*{
    padding: 0;
    margin: 0;
  }
  .menu{
    width: 220px;
    height: 620px;
    background-color: white;
    margin-left: 10px;
    margin-top: 20px;
    border-radius: 8px;
  }
  .logo{
      width: 50px;
      height: 50px;
      padding-left: 9px;
      
  }
  

  .circle {
      border: 2px solid rgb(219, 219, 219);
      background-color: #FFFFFF;
      height: 65px;
      border-radius:50%;
      width: 65px;
      display:flex ;
      align-items: center;
      
     
  }
  
  .circle span{
      padding-left: 25px;
      color: black;
      color:rgb(184, 183, 183);
  }
  span a{
      margin-left: -15px;
      }
  
  
  .about{
      width: 100%;
      height: 100%;
      border-radius: 8px;
      margin-top:20px ;
      padding: 30px 30px;
      background-color: white;
  }
  .icon{
      color:rgb(184, 183, 183);
      padding:20px ;
      flex-direction: row;
      grid-gap: 15px;
      gap: 15px;
      font-size: 20px;
     
  }
  .iconmenu:hover{
      background-color:rgba(255, 0, 0, 0.134);
      color:brown;
      cursor: pointer;
  }
  
  
      
  
      
  .links > a {
      color:rgb(184, 183, 183);
      padding-right:100px ;
      padding-bottom: 0px;
      text-align: center;
      text-decoration: none;
      transition: all .5s;
  }
  .links > a:hover {
      
      color: rgb(2, 2, 2);
  }
  .links > .line {
      background: #68a;
      height: 1px;
      pointer-events: none;
  }
  
  
  .links {
      display: grid;
      grid-template-columns: repeat(var(--items), 1fr);
      position: relative;
  }
  .links > .line {
      opacity: 0;
      transition: all .5s;
      position: absolute;
      bottom: 0;
      left: calc(100% / var(--items) * (var(--index) - 1));
      left: var(--left, calc(100% / var(--items) * (var(--index) - 1)));
      width: calc(100% / var(--items));
      width: var(--width, calc(100% / var(--items)));
      --index: 0;
  }
  .links > a:hover ~ .line {
      opacity: 1;
  }
  .address{
      margin-top: 20px;
      border: transparent;
      background-color: rgb(236, 236, 236);
  
  }
  
  
  
  
  .helpcard{
      width: 180px;
      height: 150px;
      margin-top: 20px;
      margin-left: 8px;
      font-size:small;
      align-items: center;
  }
  .helpcard h6{
      font-size:15px;
  }
  .helpcard p{
      padding-left: 30px;
  }
  .about p{
      padding-top: 20px;
  }
  ul {
      list-style-type: none;
      margin-top: 30px;
      padding: 0;
      display: flex;
      grid-gap: 20px;
      gap: 20px;
     
    }
  
    
    li {
      display: inline;
      
    }
    li a{
      text-decoration: none;
      color:rgb(143, 142, 142);
    }
    .links{
      display: flex;
      grid-gap: 40px;
      gap: 40px;
      justify-content: center;
      color:rgb(184, 183, 183); 
    }
    .linksname{
      display: flex;
      grid-gap: 25px;
      gap: 25px;
      justify-content: center;
      color:rgb(184, 183, 183); 
      font-size: x-small;
  
    }
  
   .contact{
      color:rgb(184, 183, 183); 
      
   } 
   .badge {
      position:relative;
      padding: 5px 5px;
      margin-left: 100px;
      border-radius: 50%;
      background-color: rgb(246, 221, 221);
      color:red;
   }
   .contactcard{
     display: flex;
     padding: 10px;
     
   }
  .modal-dialog {
      position:fixed;
      margin: auto;
      width: 450px;
      height: 100%;
      right: 0px;
  }
  .modal-content{
      height: 100%;
      contain: content;
      overflow-y: scroll;
      
  }
  .contactform{
      position: relative;
      padding: 10px;
  }
  .show{
      background-color: transparent;
  }
  .sidebar{
  
      margin-left: 1050px;
      height: 100%;
      background-color: white;
  }
  
  .heading{
      font-size: x-large;
      font-weight: 500;
      
  }
